import {
  FETCH_MOVIES,
  GET_SINGLE_MOVIE,
  FAILED,
  GET_SINGLE_FAIL,
  SEARCH_MOVIE,
  SEARCH_FAILED,
  CLEAR_SINGLE_MOVIE,
} from "../actions/types";

const initialState = {
  trending: [],
  random: [],
  topRated: [],
  genres: [],
  singleMovie: { movie: null, loading: true },
  loading: true,
  searchResult: { movies: [], loading: true },
};

const movies = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case FETCH_MOVIES:
      return {
        ...state,
        genres: payload.genre,
        topRated: payload.topRated,
        trending: payload.trending,
        loading: false,
      };

    case GET_SINGLE_MOVIE:
      return { ...state, singleMovie: { movie: payload, loading: false } };

    case SEARCH_MOVIE:
      return { ...state, searchResult: { movies: payload, loading: false } };

    case CLEAR_SINGLE_MOVIE:
      return { ...state, searchResult: { movies: [], loading: false } };

    case GET_SINGLE_FAIL:
      return {
        ...state,
        singleMovie: { loading: true },
      };

    case SEARCH_FAILED:
      return {
        ...state,
        searchResult: { movie: [], loading: true },
      };

    case FAILED:
      return {
        ...state,
        genre: [],
        topRated: [],
        trending: [],
        searchResult: { movie: [], loading: true },
        loading: true,
      };

    default:
      return state;
  }
};

export default movies;
