import React from "react";
import ReactDOM from "react-dom";
import { Spin } from "antd";
import styled from "styled-components";

const StyledLoader = styled.div`
  background: #fff;
  color: #fff;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 41px;

  .ant-spin-dot {
    height: 1.25em;
    width: 1.25em;
  }
  .ant-spin-lg .ant-spin-dot i {
    background-color: #f98000;
    height: 20px;
    width: 20px;
  }
`;

const LOADING = (
  <StyledLoader>
    <Spin size="large" />
  </StyledLoader>
);

function Loader() {
  return ReactDOM.createPortal(LOADING, document.getElementById("loader"));
}

export default Loader;
