import React from "react";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { changeTheme } from "../../actions/theme";

function Header({ theme, changeTheme }) {
  const StyledHeader = styled.div`
    padding: 15px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    // background: #0a2472;
    // background: #781b2b;
    background: ${theme.islight ? theme.light.header : theme.dark.header};

    a {
      color: ${theme.islight ? theme.light.headerColor : theme.dark.headerColor};
    }
    .theme-change {
      color: ${theme.islight ? theme.light.headerColor : theme.dark.headerColor};
      width: 75px;
      height: 35px;
      background: #fff;
      border-radius: 100px;
      position: relative;
      cursor: pointer;
    }

    .theme-change i {
      font-size: 20px;
      padding: 7px;
      background: #fff;
      color: #0a2472;
      border-radius: 50%;
      position: absolute;
      top: 50%;
      height: 35px;
      width: 35px;
      transform: translateY(-50%);
      transition: all 0.3s;
    }
    .theme-change.dark i {
      left: auto;
      right: 0;
    }
  `;

  const handleTheme = () => {
    changeTheme();
  };

  return (
    <StyledHeader>
      <div className="logo">
        <h2>
          <Link to="/">MoviesHub</Link>
        </h2>
      </div>
      <div
        className={`theme-change ${theme.islight ? "light" : "dark"}`}
        onClick={() => handleTheme()}
      >
        {theme.islight ? (
          <i className="fa fa-sun-o" aria-hidden="true"></i>
        ) : (
          <i className="fa fa-moon-o" aria-hidden="true"></i>
        )}
      </div>
    </StyledHeader>
  );
}

const mapStateToProps = (state) => ({ theme: state.theme });

export default connect(mapStateToProps, { changeTheme })(Header);
