import React, { useEffect } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { getSingleMovie } from "../../actions/movie";
import { Redirect } from "react-router-dom";
import Pills from "../../components/pills/Pills";
import Loader from "../../components/loader/Loader";
import defaultImg from "../../assets/img/gallery.svg";

const StyleSingle = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  min-height: 100vh;
  padding: 20px;

  .img-wrapper {
    max-width: 450px;
    min-width: 450px;
  }

  .pills-wrapper {
    display: flex;
    justify-conent: flex-start;
    align-items: center;
    padding: 10px 0;
  }

  .pills-wrapper div {
    margin-right: 10px;
  }

  & > .details {
    padding: 0 25px;
  }

  .tagline {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    font-size: 20px;
  }

  .tagline h2 {
    margin-right: 10px;
  }

  .inline {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .subtitle {
    margin-bottom: 10px;
    font-size: 20px;
  }
  .overview {
    margin: 15px 0;
  }

  .overview p {
    font-size: 16px;
  }

  .inline h4 {
    margin-right: 5px;
    font-size: 18px;
  }
  .inline p {
    font-size: 16px;
  }

  .producer-list .img_wrapper {
    width: 75px;
    height: 75px;
    margin-right: 15px;
  }

  .producer-list .img_wrapper img {
    max-height: 100%;
  }

  .producer-list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
  }

  .producer-list .producer-item {
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    // flex: 0 0 50%;
  }
  .producer-item .details {
    margin: 0;
  }
`;

function SingleMovie({
  getSingleMovie,
  movies: {
    singleMovie: { movie, loading },
  },
  match,
}) {
  useEffect(() => {
    getSingleMovie(match.params.id);
  }, [match.params.id]);

  if (loading) return <Loader />;

  if (movie == null) {
    return <Redirect to="/not-found" />;
  }

  const pill = movie.genres.map((genre) => <Pills key={genre.id} name={genre.name} />);

  return (
    <StyleSingle>
      <div className="img-wrapper">
        {movie.poster_path !== null ? (
          <img
            src={`https://image.tmdb.org/t/p/original/${movie.poster_path}`}
            // src={defaultImg}
            alt=""
          />
        ) : (
          <img src={defaultImg} alt="" />
        )}
      </div>
      <div className="details">
        <div className="title">
          <div className="tagline ">
            <h2>{movie.title}</h2>
            <h3 className="title">({movie.original_title})</h3>
          </div>
          <h4>Tagline : {movie.tagline !== "" ? movie.tagline : "No Tagline"}</h4>
        </div>
        <div className="pills-wrapper">{pill}</div>
        <div className="language inline">
          <h4>Original Language : </h4>
          <p>{movie.original_language}</p>
        </div>
        <div className="release inline">
          <h4>Release Date : </h4>
          <p>{movie.release_date.split("-").reverse().join("-")}</p>
        </div>
        <div className="runtime inline">
          <h4>Runtime : </h4> <p>{(movie.runtime / 60).toFixed(2)} hr</p>
        </div>
        <div className="status inline">
          <h4>status : </h4>
          <p>{movie.status}</p>
        </div>
        <div className="rating inline">
          <h4>Average Rating : </h4>
          <p>{movie.vote_average}, </p> <h4>Vote Count : </h4> <p>{movie.vote_count}</p>
        </div>
        <div className="overview ">
          <h3 className="subtitle">Overview</h3>
          <p>{movie.overview}</p>
        </div>
        <div className="producers ">
          <h3 className="subtitle">Producers</h3>
          <ul className="producer-list">
            {movie.production_companies.length > 0 ? (
              movie.production_companies.map((producer) => (
                <li className="producer-item" key={producer.id}>
                  <div className="img_wrapper">
                    {producer.logo_path !== null ? (
                      <img src={`https://image.tmdb.org/t/p/original/${producer.logo_path}`} alt="" />
                    ) : (
                      <img src={defaultImg} alt="" />
                    )}
                  </div>
                  <div className="details">
                    <div className="inline">
                      <h4 className="mini-title">producer : </h4>
                      <p>{producer.name}</p>
                    </div>
                    <div className="inline">
                      <h4 className="mini-title">Country : </h4>
                      <p>{producer.origin_country}</p>
                    </div>
                  </div>
                </li>
              ))
            ) : (
              <p>Producers details not found</p>
            )}
          </ul>
        </div>
      </div>
    </StyleSingle>
  );
}

const mapStateToProp = (state) => ({
  movies: state.movies,
});

export default connect(mapStateToProp, { getSingleMovie })(SingleMovie);
