import React, { useEffect, Fragment } from "react";
import Home from "./container/home/Home";
import { Switch, Route, Redirect } from "react-router-dom";
import NotFound from "./container/not-fround/NotFound";
import SingleMovie from "./container/singlemovie/SingleMovie";
import { connect } from "react-redux";
import { fetchMovies } from "./actions/movie";
import Header from "./container/header/Header";
import Search from "./components/search/Search";

const App = ({ fetchMovies, theme }) => {
  console.log(theme);

  useEffect(() => {
    fetchMovies();
  }, []);

  useEffect(() => {
    console.log(theme.light.background, theme.dark.background);
    document.body.style.background = theme.islight ? theme.light.background : theme.dark.background;
  }, [theme]);

  return (
    <Fragment>
      <Header />
      <Search />
      <Switch>
        <Route path="/not-found" component={NotFound} />
        <Route exact path="/" component={Home} />
        <Route path="/movie/:id" component={SingleMovie} />
        <Redirect to="/not-found" />
      </Switch>
    </Fragment>
  );
};

const mapStateToProps = (state) => ({
  theme: state.theme,
});

export default connect(mapStateToProps, { fetchMovies })(App);
