import React from "react";
import { createPortal } from "react-dom";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import defaultImg from "../../assets/img/gallery.svg";

const StyledResult = styled.div`
  position: absolute;
  top: 120px;
  right: 20px;
  left: 20px;
  min-height: 100px;
  background: #fff;
  z-index: 99;
  padding: 20px;
  border-radius: 10px;
  border-width: 1px;
  border-style: solid;
  border-color: rgb(207, 207, 207);
  box-shadow: rgba(28, 28, 28, 0.08) 0px 2px 8px;
  background: #ececec;

  .search-title {
    font-size: 20px;
    border-radius: 10px;
    padding-bottom: 15px;
    position: relative;
  }

  .search-title:before {
    content: "";
    position: absolute;
    bottom: 10px;
    background: #0c2271;
    height: 3px;
    width: 30px;
  }

  .result-list {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin: 0;
  }

  .result-item {
    flex: 0 0 32%;
  }

  .no-movies-found {
    flex: 0 0 100%;
    margin: 0;
    background: #fff;
    padding: 10px;
    border-radius: 10px;
    font-size: 20px;
    text-align: center;
  }

  .result-item a {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    text-decoration: none;
    color: #000;
    font-size: 18px;
    border: 1px solid transparent;
    border-radius: 10px;
    background: #fff;
    padding: 5px;
    border-radius: 10px;
    margin: 0 0 10px 0;
    tranisition: all 0.4s;

    &:hover {
      background: #0c2271;
      color: #fff;
    }
  }

  .result-item .img-wrapper img {
    width: 45px;
    min-width: 45px;
    height: 45px;
    border-radius: 10px;
    border: 1px solid #fff;
  }

  .result-item a .title {
    margin-left: 10px;
  }
`;
// : { movies, loading }
function SearchResult({ searchResult: { movies, loading }, trending, show, hideSearch, value }) {
  const trendingResult =
    trending !== undefined ? (
      trending.slice(0, 18).map((movie) => (
        <li className="result-item" key={movie.id}>
          <Link to={`/movie/${movie.id}`} onClick={() => hideSearch()}>
            <div className="img-wrapper">
              {movie.poster_path !== null ? (
                <img src={`https://image.tmdb.org/t/p/original/${movie.poster_path}`} alt="" />
              ) : (
                <img src={defaultImg} alt="" />
              )}
            </div>
            <div className="title">{movie.original_title}</div>
          </Link>
        </li>
      ))
    ) : (
      <p>No Movies</p>
    );

  const movieResult =
    movies !== undefined && movies.length > 0 ? (
      movies.slice(0, 18).map((movie) => (
        <li className="result-item" key={movie.id}>
          <Link to={`/movie/${movie.id}`} onClick={() => hideSearch()}>
            <div className="img-wrapper">
              {movie.poster_path !== null ? (
                <img src={`https://image.tmdb.org/t/p/original/${movie.poster_path}`} alt="" />
              ) : (
                <img src={defaultImg} alt="" />
              )}
            </div>
            <div className="title">{movie.original_title}</div>
          </Link>
        </li>
      ))
    ) : (
      <li className="no-movies-found">No Movies</li>
    );

  const SEARCH = show ? (
    <StyledResult>
      {value !== "" ? (
        <h2 className="search-title">Search Result</h2>
      ) : (
        <h2 className="search-title">Trending Search</h2>
      )}

      {!loading ? (
        <ul className="result-list">{value === "" ? trendingResult : movieResult}</ul>
      ) : (
        <ul className="result-list">{trendingResult}</ul>
      )}
    </StyledResult>
  ) : null;

  return createPortal(SEARCH, document.getElementById("search-result"));
}

const mapStateToProps = (state) => ({
  searchResult: state.movies.searchResult,
  trending: state.movies.trending,
});

export default connect(mapStateToProps)(SearchResult);
