import React from "react";
import Card from "../card/Card";
import { connect } from "react-redux";
import Title from "../title/Title";
import Slider from "react-slick";
import { RightCircleFilled, LeftCircleFilled } from "@ant-design/icons";
import styled from "styled-components";

const topSettings = {
  // dots: true,
  infinite: true,
  slidesToShow: 4,
  slidesToScroll: 1,
  nextArrow: <RightCircleFilled />,
  prevArrow: <LeftCircleFilled />,
};

const trendingSettings = {
  // dots: true,
  infinite: true,
  slidesToShow: 6,
  slidesToScroll: 1,
  nextArrow: <RightCircleFilled />,
  prevArrow: <LeftCircleFilled />,
};

const StyledSlider = styled.div`
  .slick-slider {
    position: relative;
  }

  .slick-prev,
  .slick-next {
    position: absolute;

    font-size: 40px;
  }
  .slick-prev {
    top: -35px;
    right: 75px;
  }
  .slick-next {
    top: -35px;
    right: 25px;
  }
`;

function SliderMovie({ genres, movies, title, type }) {
  let settings;
  if (type === "top") {
    settings = topSettings;
  } else {
    settings = trendingSettings;
  }

  const trendingMovies = movies.map((movie) => {
    return (
      <Card
        key={movie.id}
        id={movie.id}
        img={`https://image.tmdb.org/t/p/original/${movie.poster_path}`}
        title={movie.title.length < 15 ? movie.title : movie.title.substring(0, 20) + "..."}
        language={movie.original_language}
        release={movie.release_date}
        rating={movie.vote_average}
        slideType={type}
      />
    );
  });

  return (
    <StyledSlider>
      <Title title={title} />
      {/* <Row gutter={16}>{trendingMovies}</Row> */}
      <Slider {...settings}>{trendingMovies}</Slider>
    </StyledSlider>
  );
}

const mapStateToProp = (state) => ({
  genres: state.movies.genres,
});

export default connect(mapStateToProp)(SliderMovie);
