import React from "react";
import { connect } from "react-redux";
import Banner from "../../components/banner/Banner";
import SliderMovie from "../../components/slidersection/SliderMovie";
import Loader from "../../components/loader/Loader";
import styled from "styled-components";

const StyledHome = styled.div`
  padding: 20px;
`;

const Home = ({ movies: { trending, topRated, loading } }) => {
  if (loading) return <Loader />;

  return (
    <StyledHome>
      <Banner />
      <SliderMovie movies={topRated} type="top" title="Top Rated Today" />
      <SliderMovie movies={trending} type="trending" title="Trending Today" />
    </StyledHome>
  );
};

const mapStateToProps = (state) => ({
  movies: state.movies,
});

export default connect(mapStateToProps)(Home);
