import React from "react";
import styled from "styled-components";
import { connect } from "react-redux";

function Title({ title, theme }) {
  const StyledTitle = styled.h2`
font-size: 28px;
position:relative;
padding-bottom:7px;
margin-bottom:10px;
color:${theme.islight ? theme.light.backgound : theme.dark.backgound}

&:before{
    content: '';
    position: absolute;
    bottom: 0;
    left: 0;
    width: 35px;
    height: 5px;
    background: ${theme.islight ? theme.light.backgound : theme.dark.backgound};
}
}
`;

  return <StyledTitle>{title}</StyledTitle>;
}

const mapStateToProp = (state) => ({
  theme: state.theme,
});

export default connect(mapStateToProp)(Title);
