import axios from "axios";
import {
  BASE_URL,
  API_KEY,
  FETCH_MOVIES,
  GET_SINGLE_MOVIE,
  FAILED,
  GET_SINGLE_FAIL,
  SEARCH_MOVIE,
  SEARCH_FAILED,
  CLEAR_SINGLE_MOVIE,
} from "./types";

export const fetchMovies = () => async (dispatch) => {
  try {
    const treandingRes = await axios.get(`${BASE_URL}/trending/movie/day${API_KEY}`);
    const trending = treandingRes.data.results;

    const topRatedRes = await axios.get(`${BASE_URL}/movie/top_rated/${API_KEY}`);
    const topRated = topRatedRes.data.results;

    const genreRes = await axios.get(`${BASE_URL}/genre/movie/list${API_KEY}`);
    const genre = genreRes.data.genres;

    dispatch({
      type: FETCH_MOVIES,
      payload: { genre, topRated, trending },
    });
  } catch (err) {
    console.log(err);
    dispatch({ type: FAILED });
  }
};

export const getSingleMovie = (id) => async (dispatch) => {
  try {
    dispatch({
      type: CLEAR_SINGLE_MOVIE,
    });
    const res = await axios.get(`${BASE_URL}/movie/${id}${API_KEY}`);

    dispatch({
      type: GET_SINGLE_MOVIE,
      payload: res.data,
    });
  } catch (err) {
    console.log(err);
    dispatch({ type: GET_SINGLE_FAIL });
  }
};

export const searchMovie = (query) => async (dispatch) => {
  try {
    const res = await axios.get(`${BASE_URL}/search/movie${API_KEY}&query=${query}`);
    // console.log(res.data);
    dispatch({ type: SEARCH_MOVIE, payload: res.data.results });
  } catch (err) {
    console.log(err);
    dispatch({ type: SEARCH_FAILED });
  }
};
