import React, { useState } from "react";
import styled from "styled-components";
import { connect } from "react-redux";
import { searchMovie } from "../../actions/movie";
import SearchResult from "../searchresult/SearchResult";

const StyledSearch = styled.div`
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  background: #fff;

  input {
    width: 100%;
    border: none;
    padding: 10px;
    outline: none;
    font-size: 18px;
  }

  button {
    border: none;
    background: transparent;
    outline: none;
    cursor: pointer;
  }

  i {
    padding: 10px 20px;
  }
`;

function Search({ searchMovie }) {
  const [showSearch, setShowSearch] = useState(false);
  const [inputVal, setInputVal] = useState("");

  const handleFocus = () => {
    setShowSearch(true);
  };
  const handleBlur = () => {
    console.log("blur", showSearch);
    setShowSearch(false);
  };

  const handleNull = (e) => {
    searchMovie(e.target.value);
    setInputVal(e.target.value);
  };

  return (
    <StyledSearch>
      <input
        type="text"
        placeholder="Search Movie"
        onChange={(e) => handleNull(e)}
        onFocus={() => handleFocus()}
        value={inputVal}
      />
      <button onClick={() => handleBlur()}>
        <i className={!showSearch ? "fa fa-search" : "fa fa-times"}></i>
      </button>
      <SearchResult show={showSearch} hideSearch={handleBlur} value={inputVal} />
    </StyledSearch>
  );
}

export default connect(null, { searchMovie })(Search);
