import { CHANGE_THEME } from "../actions/types";

const initialState = {
  islight: true,
  light: {
    header: "#f2545b",
    headerColor: "#fff",
    color: "#f2545b",
    background: "#fff",
  },
  dark: {
    header: "#781b2b",
    headerColor: "#fff",
    background: "#781b2b",
    color: "#fff",
  },
};

const theme = (state = initialState, action) => {
  const { type, payload } = action;

  switch (type) {
    case CHANGE_THEME:
      return { ...state, islight: !state.islight };

    default:
      return state;
  }
};

export default theme;
