import React from "react";
import styled from "styled-components";

const Styledpill = styled.div`
  background: #ffdcd5;
  border-radius: 100px;
  max-width: 100px;
  padding: 5px 15px;
  color: #de6952;
  font-weight: 600;
  display: flex;
  text-align: center;
  justify-content: center;
  align-items: center;
`;

function Pills({ name }) {
  return <Styledpill>{name}</Styledpill>;
}

export default Pills;
