import React from "react";
import gallery from "../../assets/img/gallery.svg";
import { StarFilled } from "@ant-design/icons";
import styled from "styled-components";
import { Link } from "react-router-dom";
import { connect } from "react-redux";
import { getSingleMovie } from "../../actions/movie";

const StyledCard = styled.div`
  &.card-trending {
    padding: 5px;
  }
  padding: 10px;

  img {
    border-rdius: 10px;
  }

  .title {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .release {
    margin: 0;
  }
  .rating {
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  .rating p {
    margin: 0;
    margin-left: 5px;
  }
`;

function Card({ id, img, title, language, release, rating, slideType, getSingleMovie }) {
  return (
    <StyledCard className={`card-${slideType}`}>
      <div className="card_header">
        <div className="img_wrapper">{img == null ? <img src={gallery} alt="" /> : <img src={img} alt="" />}</div>
      </div>
      <div className="card_body">
        <div className="title">
          <h2>
            <Link to={`/movie/${id}`} onClick={() => getSingleMovie(id)}>
              {title}
            </Link>
          </h2>
          <p className="lang">{language}</p>
        </div>
        <p className="release">{release}</p>
        <div className="rating">
          <StarFilled />
          <p className="rating_count">{rating}</p>
        </div>
      </div>
    </StyledCard>
  );
}

export default connect(null, { getSingleMovie })(Card);
